.contact-page {
  display: flex;
  align-content: center;
  gap: 80px;
  flex-direction: row;
  width: 100%;
  justify-content: left;
  padding: 0 5%;
  opacity: 1;
  animation: fadeIn 1.8s;
  .text-zone {
    width: 50%;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    height: 100%;
    max-height: 90%;
    h1 {
      color: white;
      font-size: 53px;
      font-family: "Coolvetica", sans-serif;
      font-weight: 400;
      animation: fadeInLeft 1s forwards;
      display: flex;
      flex-direction: column;

      &:before {
        content: "<h1>";
        font-family: "La Belle Aurore", sans-serif;
        color: yellow;
        font-size: 18px;
        margin-top: -40px;
        opacity: 0.6;
        animation: fadeIn 1s 1.8s backwards;
      }

      &:after {
        content: "<h1>";
        font-family: "La Belle Aurore", sans-serif;
        color: yellow;
        font-size: 18px;
        margin-top: 18px;
        opacity: 0.6;
        animation: fadeIn 1s 1.8s backwards;
      }


    }

    .contact-form {
      form {
        display: flex;
        flex-direction: column;
        gap: 10px;

        .row {
          display: flex;
          gap: 10px;

          input {
            width: 50%;
          }
        }

        input {
          width: 100%;
          padding: 12px 20px;
          box-sizing: border-box;
          border: 1px solid yellow;
          outline: none;
          background: #022c43;
          color: white;
        }

        input:focus, textarea:focus {
          background-color: #53818b;
          color: white;
        }

        textarea {
          width: 100%;
          height: 100px;
          padding: 12px 20px;
          box-sizing: border-box;
          border: 1px solid yellow;
          outline: none;
          background: #022c43;
          color: white;
        }

        .flat-button {
          color: white;
          max-width: fit-content;
          font-size: 13px;
          font-weight: 400;
          letter-spacing: 4px;
          padding: 10px 24px;
          margin-left: auto;
          margin-top: 10px;
          text-decoration: none;
          white-space: nowrap;
          border-radius: 4px;

          &:hover {
            background-color: yellow;
            color: #333;
          }
        }
      }
    }
  }

  .info-map {
    position: relative;
    width: 50%;
    .contact-info {
      position: absolute;
      top: 100px;
      left: 0;
      background: black;
      color: white;
      padding: 20px 20px;
      z-index: 10;
    }

    .map-wrapper {
      background: rgba(8, 253, 216, 0.1);
      width: 100%;
      height: 100%;

      .leaflet-container{
        height: 100%;
        width: 100%;
        position: relative;
        z-index: 1;
      }
    }
  }
}


